
.create_the_team_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-area: content;
    padding: 5px;
}
.create_the_team_container h1 {
    font-size: xx-large;
}
.create_team_inputs{
    height: 60vh;
    line-height: 2;
    justify-content: center;

    padding: 0px 50px;
    display: flex;
    text-align: center;
    flex-direction: column;
   
}
.passwordFirstInput{
     
    display: flex;
    flex-direction: row;
}
.onInputError {
    display: inline-block;
    height: 1.5rem;
    line-height: 1.5rem;
    color: rgb(250, 76, 76);
}
.create_team_inputs{
    height: 60vh;
    line-height: 2;
    justify-content: center;

    padding: 0px 50px;
    display: flex;
    text-align: center;
    flex-direction: column;
   
}
#passwordToggle{
    background-color: rgb(4, 4, 63);
    width: 2rem;
    height: 2.1rem;
    border-radius: 5px;
    margin-left: 3px;
}
.google-auth {
    background-color: white;
    color: black;
    width: max-content;
    padding: 5px 10px;
    margin-top: 20px;
}
#submit_reg{
    height: 2.5rem;
    width: 100%;
}
@media (max-width : 750px) {
    .create_the_team_container{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5px;
    }
    .create_the_team_container h1 {
        font-size: xx-large;
    }
    .create_the_team_container h1 span {
        color: orangered;
    }
    .create_the_team_container input {
        background-color: rgb(77, 77, 77);
        margin-bottom: 3px;
        
    }
    .passwordFirstInput{
     
        display: flex;
        flex-direction: row;
    }
    .onInputError {
        display: inline-block;
        height: 1.5rem;
        line-height: 1.5rem;
        color: rgb(250, 76, 76);
    }
    .create_team_inputs{
        height: 60vh;
        line-height: 2;
        justify-content: center;
    
        padding: 0px 50px;
        display: flex;
        text-align: center;
        flex-direction: column;
       
    }
    #passwordToggle{
        background-color: rgb(4, 4, 63);
        width: 2rem;
        height: 2.1rem;
        border-radius: 5px;
        margin-left: 3px;
    }
    #submit_reg{
        height: 2.5rem;
    }
}